exports.next = (comp)=>{
    let a= comp.state.a + 1
    a= a > 604 ? 1 : a
    let b = a + 1 
    comp.setState({ a, b })
}
exports.prev = (comp)=>{
    let a = comp.state.a - 1
    a = a < 1 ? 603: a
    let b = a + 1 
    comp.setState({ a, b })
}
exports.handleChange = (event,comp)=>{
     comp.setState({
        [event.target.name] : event.target.value
    })
}
exports.goTo = (event,comp) =>{
    event.preventDefault()
    let a = parseInt(comp.state.value)
    a = a < 1 ? 603: a > 604 ? 1 : a
    let b = a + 1 
    comp.setState({ a, b })
 }
