import React, { PureComponent } from "react";
import Utility from "./Utility"

export default class Alquran extends PureComponent {
  state ={
       a : 1,
       b : 2,
       value : ""
  }

  render() {
    
    let {a , b , value} = this.state
    a = a < 10 ? "00" + a : a < 100 ? "0" + a : a
    b = b === 605 ? 1 : b
    b = b < 10 ? "00" + b : b < 100 ? "0" + b : b
    return (
      <div className="Alquran">
        <div className="fixed top-0 bg-clrgreen p-0 h-2 w-full m-0"></div>
        <div className="fixed bottom-0 bg-clrgreen p-0 h-2 w-full m-0"></div>
        <i className="fa-solid fa-list fa-xl absolute nadu md:right-8 md:top-8 right-2 top-6"></i>
        <i className="fa-solid fa-book text-3xl md:text-[70px] absolute nadu md:right-8 -right-2 -bottom-2 md:bottom-0"></i>
        <form onSubmit={(event)=>Utility.goTo(event,this)} className="form">
           <input type="text" className="w-full h-5/6 bg-transparent outline-none placeholder:text-clrgreen" onChange={(event)=>Utility.handleChange(event,this) } placeholder="Enter Page.no" value={value} name="value" />
        </form>

        {/* container */}
        <div className="fixed container w-[255px] md:w-[510px]  h-[480px] top-1/2 left-1/2">{/*main div*/}
         <div className=" w-full h-[420px] flex">
          <img src={"https://mp3quran.net/api/quran_pages_svg/"+b.toString() + ".svg"} className="h-full w-[250px] hidden md:block" alt="" />
          <img src={"https://mp3quran.net/api/quran_pages_svg/"+a.toString() +".svg"} className="h-full w-[250px]" alt="" />
         </div>
        {/* contrller */}
          <span className="ARROW absolute flex -bottom-[5%] md:-bottom-[2%] left-2/4 w-64 h-10 items-center justify-center">
            <div title="Arrow Left" onClick={()=>Utility.next(this)} className="m-2 cursor-pointer"><i className="fa-solid fa-angles-left"></i>NEXT</div>
            <div id="val" name="value" className="m-2 ml-9 mr-9">{a}</div>
            <div title="Arrow Right" onClick={()=>Utility.prev(this)} className="m-2 cursor-pointer">PREV<i className="fa-solid fa-angles-right"></i></div>
          </span>
        </div>
      </div>
    );
  }
}
